<template>
  <div class="app-container">
    <!-- 头部 -->
    <div class="filter-container">
      <div class="back">&lt;会员管理</div>
      <p style="margin-left: 50px; display: inline-block;">查找：</p>
      <el-input v-model="listQuery.author" placeholder="手机/姓名/昵称" style="width: 200px;" class="filter-item" @keyup.enter.native="handleFilter" />
      <p style="margin-left: 10px; display: inline-block;">性别：</p>
      <el-select v-model="listQuery.sex" placeholder="" clearable class="filter-item" style="width: 80px">
        <el-option v-for="item in calendarTypeOptions" :key="item.key" :label="item.key" :value="item.key" />
      </el-select>
      <p style="margin-left: 10px; display: inline-block;">注册时间：</p>
      <el-date-picker
        v-model="value1"
        type="daterange"
        range-separator="至"
        start-placeholder="开始日期"
        end-placeholder="结束日期">
      </el-date-picker>
      <el-button v-waves class="filter-item" style="margin-left: 10px;" type="primary" icon="el-icon-search" @click="handleFilter">
        搜索
      </el-button>
    </div>
    <!-- 表格 -->
    <el-table
      :key="tableKey"
      v-loading="listLoading"
      :data="list"
      border
      fit
      highlight-current-row
      style="width: 100%;"
      @sort-change="sortChange"
    >
      <el-table-column label="序号" prop="id" sortable align="center" width="80" :class-name="getSortClass('id')">
        <template slot-scope="{row}">
          <span>{{ row.id }}</span>
        </template>
      </el-table-column>

      <el-table-column label="帐号"  align="center">
        <template slot-scope="{row}">
          <span>{{row.telephone}}</span>
        </template>
      </el-table-column>
      <el-table-column label="昵称"  align="center">
        <template slot-scope="{row}">
          <span>{{row.reviewer}}</span>
        </template>
      </el-table-column>
      <el-table-column label="姓名"  align="center">
        <template slot-scope="{row}">
          <span>{{row.author}}</span>
        </template>
      </el-table-column>
      <el-table-column label="性别"  align="center">
        <template slot-scope="{row}">
          <span>{{row.sex}}</span>
        </template>
      </el-table-column>

      <el-table-column label="注册时间"  align="center">
        <template slot-scope="{row}">
          <span>{{ row.timestamp | parseTime('{y}-{m}-{d} {h}:{i}') }}</span>
        </template>
      </el-table-column>

      <el-table-column label="操作" align="center" class-name="small-padding fixed-width">
        <template slot-scope="{row}">
          <el-button type="primary" size="mini" @click="jupmDetails(row)">
            查看详情
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 翻页 -->
    <pagination v-show="total>0" :total="total" :page.sync="listQuery.page" :limit.sync="listQuery.limit" @pagination="getList" />
  </div>
</template>

<script>
import { fetchList } from '@/api/article'
import waves from '@/directive/waves' // waves directive
import { parseTime } from '@/utils'
import Pagination from '@/components/Pagination' // secondary package based on el-pagination

const calendarTypeOptions = [
  { key: '1' },
  { key: '2' }
]

// 性别搜索
const calendarTypeKeyValue = calendarTypeOptions.reduce((acc, cur) => {
  acc[cur.key] = cur.display_name
  return acc
}, {})

export default {
  name: 'carModel',
  components: { Pagination },
  directives: { waves },
  filters: {
    statusFilter(status) {
      const statusMap = {
        published: 'success',
        draft: 'info',
        deleted: 'danger'
      }
      return statusMap[status]
    },
    typeFilter(type) {
      return calendarTypeKeyValue[type]
    }
  },
  data() {
    return {
      value1: '',
      tableKey: 0,
      sex: '',
      list: null,
      total: 0,
      listLoading: false,
      listQuery: {
        page: 1,
        limit: 20,
        importance: '',
        title: undefined,
        type: undefined,
        author: '',
        sort: '+id'
      },
      calendarTypeOptions,
      temp: {
        id: undefined,
        importance: 1,
        remark: '',
        timestamp: new Date(),
        title: '',
        type: '',
        author: '',
        status: 'published'
      }
    }
  },
  created() {
    this.getList()
  },
  methods: {
    // 获取列表
    getList() {
      this.listLoading = true
      fetchList(this.listQuery).then(response => {
        this.list = response.data.items
        this.total = response.data.total
        console.log(this.list)
        console.log(this.sex)
        // 模拟请求延迟
        setTimeout(() => {
          this.listLoading = false
        }, 1.5 * 1000)
      })
    },
    handleFilter() {
      this.listQuery.page = 1
      this.getList()
      console.log(this.listQuery.author)
    },
    jupmDetails(row) {
      console.log('跳转', row)
      const data = JSON.stringify(row)
      this.$router.push({
        name: 'MemberInfo',
        query: {
          data
        }
      })
    },
    // json格式化
    formatJson(filterVal) {
      return this.list.map(v => filterVal.map(j => {
        if (j === 'timestamp') {
          return parseTime(v[j])
        } else {
          return v[j]
        }
      }))
    },
    getSortClass: function(key) {
      const sort = this.listQuery.sort
      return sort === `+${key}` ? 'ascending' : 'descending'
    },
    sortChange() {}
  }
}
</script>

<style lang="scss">
.app-container{
  margin-top: 10px;
}
  .back{
    display: inline-block;
  }
</style>
